.main-nav__toggle {
  position: absolute;
  top: 20px;
  left: auto;
  right: 20px;
  z-index: 2;
  display: block;
  width: 25px;
  height: 25px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle {
  position: absolute;
  top: 20px;
  left: auto;
  right: 20px;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  transition: 0.3s;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after,
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: rgba(104, 183, 56, 0.8);
  box-shadow: 0 8px 0 0 rgba(104, 183, 56, 0.8), 0 16px 0 0 rgba(104, 183, 56, 0.8);
  transition: 0.3s;
}
.main-nav__list {
  position: relative;
  z-index: 10;
  list-style: none;
  padding: 0;
  margin: 0;
  transition: ease 1s all;
}
