.advantage {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.advantage__title {
  font-family: "oswaldregular", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 40px;
  margin: 20px 0 40px 0;

  color: @base-color;

  @media (min-width: @tablet-width) {
    margin: 45px 0 35px 0;
    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 30px 0;
  }
}

.advantage__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 45px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 205px;
  }
}

.advantage__item {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  margin: 0 0 22px 80px;
  position: relative;

  &:last-child {
    margin-bottom: 42px;
  }

  @media (min-width: @tablet-width) {
    width: 220px;
    padding: 150px 30px 60px 0;
    margin: 0;

    background-image: url("../img/1desktop.svg");
    background-repeat: no-repeat;
    background-position: 130px;

    &:nth-child(2) {
      background-image: url("../img/2desctop.svg");
      background-repeat: no-repeat;
      background-position: 130px;
    }

    &:nth-child(3) {
      background-image: url("../img/3desctop.svg");
      background-repeat: no-repeat;
      background-position: 130px;
    }

    &:last-child {
      margin-bottom: 0;
      background-image: url("../img/4tablet.svg");
      background-repeat: no-repeat;
      background-position: 130px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 250px;
    font-size: 16px;
    line-height: 24px;
  }
}

.advantage__item--leaf-icon::before {
  content: "";
  background-image: url("../img/advantage-leaf.svg");
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
  width: 60px;
  height: 60px;
  position: absolute;
  left: -80px;

  @media (min-width: @tablet-width) {
    width: 80px;
    height: 80px;
    left: 0;
    top: 30px;
  }
}

